"use client";

import { useEffect, useMemo, useState } from "react";
import { useParams } from "next/navigation";
import { collectionData } from "@/data/collectionData";
import { Button } from "@/components/ui/button";
import { Slider } from "@/components/ui/slider";
import { Checkbox } from "@/components/ui/checkbox";
import { Card, CardContent } from "@/components/ui/card";
import { ScrollArea } from "@/components/ui/scroll-area";

const ProductPage = () => {
  const [products, setProducts] = useState([]);
  const [nameCollection, setNameCollection] = useState("");
  const [price, setPrice] = useState(0);
  const [category, setCategory] = useState("");
  const [size, setSize] = useState("");
  const [color, setColor] = useState("");
  const [rating, setRating] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);

  const itemsPerPage = 12;
  const params = useParams();

  useEffect(() => {
    const collection = collectionData.find(
      (collection) => collection.href === params?.collection
    );

    if (collection) {
      setProducts(collection.products);
      setNameCollection(collection.name);
    }
  }, [params?.collection]);

  const filtered = useMemo(() => {
    return products.filter((product) => {
      const matchCategory = category ? product.category === category : true;
      const matchSize = size ? product.size === size : true;
      const matchColor = color ? product.color === color : true;
      const matchRating = rating ? product.rating >= rating : true;
      const matchPrice = price ? product.price <= price : true;

      return matchCategory && matchSize && matchColor && matchRating && matchPrice;
    });
  }, [products, category, size, color, rating, price]);

  const paginatedProducts = useMemo(() => {
    const start = (currentPage - 1) * itemsPerPage;
    const end = start + itemsPerPage;
    return filtered.slice(start, end);
  }, [filtered, currentPage]);

  const totalPages = Math.ceil(filtered.length / itemsPerPage);

  useEffect(() => {
    setCurrentPage(1);
  }, [category, size, color, rating, price]);

  const handleClearFilters = () => {
    setCategory("");
    setSize("");
    setColor("");
    setRating(0);
    setPrice(0);
  };

  return (
    <div className="flex flex-col lg:flex-row w-full min-h-screen p-4 gap-4">
      {/* الفلاتر */}
      <ScrollArea className="lg:w-1/4 lg:max-h-screen sticky top-0 rounded-md border p-4">
        <div className="mb-4">
          <h2 className="text-xl font-semibold mb-2">الفئة</h2>
          {["رجالي", "نسائي", "أطفال"].map((cat) => (
            <div key={cat} className="flex items-center space-x-2 mb-2">
              <Checkbox id={cat} checked={category === cat} onCheckedChange={() => setCategory(cat)} />
              <label htmlFor={cat}>{cat}</label>
            </div>
          ))}
        </div>

        <div className="mb-4">
          <h2 className="text-xl font-semibold mb-2">المقاس</h2>
          {["S", "M", "L", "XL", "XXL"].map((s) => (
            <div key={s} className="flex items-center space-x-2 mb-2">
              <Checkbox id={s} checked={size === s} onCheckedChange={() => setSize(s)} />
              <label htmlFor={s}>{s}</label>
            </div>
          ))}
        </div>

        <div className="mb-4">
          <h2 className="text-xl font-semibold mb-2">اللون</h2>
          {["أحمر", "أزرق", "أسود", "أبيض"].map((c) => (
            <div key={c} className="flex items-center space-x-2 mb-2">
              <Checkbox id={c} checked={color === c} onCheckedChange={() => setColor(c)} />
              <label htmlFor={c}>{c}</label>
            </div>
          ))}
        </div>

        <div className="mb-4">
          <h2 className="text-xl font-semibold mb-2">التقييم</h2>
          {[5, 4, 3, 2, 1].map((r) => (
            <div key={r} className="flex items-center space-x-2 mb-2">
              <Checkbox id={`rating-${r}`} checked={rating === r} onCheckedChange={() => setRating(r)} />
              <label htmlFor={`rating-${r}`}>{r} نجوم فأعلى</label>
            </div>
          ))}
        </div>

        <div className="mb-4">
          <h2 className="text-xl font-semibold mb-2">السعر الأقصى: {price} جنيه</h2>
          <Slider defaultValue={[price]} max={1000} step={10} onValueChange={(val) => setPrice(val[0])} />
        </div>

        <Button variant="outline" onClick={handleClearFilters} className="w-full mt-4">
          مسح الفلاتر
        </Button>
      </ScrollArea>

      {/* المنتجات */}
      <main className="w-full lg:w-3/4">
        <h1 className="text-3xl font-bold mb-6">{nameCollection}</h1>

        <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 xl:grid-cols-4 gap-6">
          {paginatedProducts.map((product) => (
            <Card key={product.name}>
              <CardContent className="p-0">
                <img src={product.image} alt={product.name} className="w-full h-60 object-cover" />
                <div className="p-4">
                  <h2 className="text-lg font-semibold">{product.name}</h2>
                  <p className="text-orange-500 font-bold">{product.price} جنيه</p>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>

        {/* ترقيم الصفحات */}
        <div className="flex justify-center mt-8 gap-2">
          {Array.from({ length: totalPages }, (_, i) => i + 1).map((page) => (
            <Button
              key={page}
              variant={page === currentPage ? "default" : "outline"}
              onClick={() => setCurrentPage(page)}
              className="w-10 h-10 p-0"
            >
              {page}
            </Button>
          ))}
        </div>
      </main>
    </div>
  );
};

export default ProductPage;
